input.search-location {
	border: 1px solid var(--general-2);
	height: 40px !important;
	border-radius: 4px;

	&:focus {
		outline: unset !important;
	}

	&::placeholder {
		font-style: italic !important;
		color: var(--general-2) !important;
	}
}

.search-option {
	min-height: 45px;
	height: 45px;
	border-bottom: 1px solid var(--neutral-1);
}

mat-label {
	margin-bottom: 8px;
}

.address-area {
	resize: none;
}

.mat-mdc-option.mat-mdc-option-active.mdc-list-item:first-of-type {
	background-color: transparent !important;
}

.mat-mdc-button.eir-btn {
	--mat-mdc-button-ripple-color: transparent !important;
	--mat-mdc-button-persistent-ripple-color: transparent !important;
}

.py-5px {
	padding-top: 5.5px;
	padding-bottom: 5.5px;
}

.mat-icon {
	margin-right: 4px !important;
}

div.search-container {
	position: sticky;
	top: 0;
	width: 100%;
	background: var(--basic-1);
	z-index: 9999;
}

input.eircode-search {
	&::placeholder {
		font-style: italic !important;
		color: var(--general-2) !important;
	}
}
